html {
  background-color: #fff;
  text-size-adjust: 100%;
}

body {
  margin: 0;
  color: #111827;
}

.fs-link {
  color: #3B82F6;
  text-decoration: underline;
}
.fs-link:not(.fs-link--no-visited):visited {
  color: #8B5CF6;
}
.fs-link:hover {
  color: #2f68c5;
}
.fs-link:active {
  color: #111827;
}

.fs-main-wrapper {
  display: block;
  max-width: 60rem;
  margin-right: 0.9375rem;
  margin-left: 0.9375rem;
}
@supports (margin: max(calc(0px))) {
  .fs-main-wrapper {
    margin-right: max(0.9375rem, calc(0.9375rem + env(safe-area-inset-right)));
    margin-left: max(0.9375rem, calc(0.9375rem + env(safe-area-inset-left)));
  }
}
@media screen and (min-width: 541px) {
  .fs-main-wrapper {
    margin-right: 1.875rem;
    margin-left: 1.875rem;
  }
  @supports (margin: max(calc(0px))) {
    .fs-main-wrapper {
      margin-right: max(1.875rem, calc(0.9375rem + env(safe-area-inset-right)));
      margin-left: max(1.875rem, calc(0.9375rem + env(safe-area-inset-left)));
    }
  }
}
@media screen and (min-width: 63.75rem) {
  .fs-main-wrapper {
    margin-right: auto;
    margin-left: auto;
  }
  @supports (margin: max(calc(0px))) {
    .fs-main-wrapper {
      margin-right: auto;
      margin-left: auto;
    }
  }
}

