@use "sass:string";
@use "sass:color";

// Color selection function
@function fs-color($color) {
	$color: string.quote($color);
	@return map-get($fs-color-palette, $color);
}

// Color tweaking functions 
@function fs-color-shade($color, $percentage) {
	@return color.mix(#000000, $color, $percentage);
}

@function fs-color-tint($color, $percentage) {
	@return color.mix(#FFFFFF, $color, $percentage);
}

// Color palette
$fs-color-palette: (
	"red": #EF4444,
	"yellow": #F59E0B,
	"green": #10B981,
	"blue": #3B82F6,
	"indigo": #6366F1,
	"purple": #8B5CF6,
	"pink": #EC4899,
	"white": #fff,
	"black": #000,
	"grey5": #F9FAFB,
	"grey10": #F3F4F6,
	"grey20": #E5E7EB,
	"grey30": #D1D5DB,
	"grey40": #9CA3AF,
	"grey50": #6B7280,
	"grey60": #4B5563,
	"grey70": #374151,
	"grey80": #1F2937,
	"grey90": #111827
) !default;

// Usage defined colours
$fs-brand-color: fs-color("blue") !default;
$fs-text-color: fs-color("grey90") !default;
$fs-text-color-heading: $fs-text-color !default;
$fs-text-color-secondary: fs-color("grey50") !default;
$fs-canvas-background-color: fs-color("white") !default;
$fs-body-background-color: $fs-canvas-background-color !default;
$fs-focus-color: fs-color("yellow") !default;
$fs-focus-text-color: fs-color("black") !default;
$fs-error-color: fs-color("red") !default;
$fs-success-color: fs-color("green") !default;
$fs-border-color: fs-color("grey30") !default;
$fs-link-color: fs-color("blue") !default;
$fs-link-visited-color: fs-color("purple") !default;
$fs-link-hover-color: fs-color-shade(fs-color("blue"), 20%) !default;
$fs-link-active-color: $fs-text-color !default;
