@use "sass:map";

// Breakpoints
$fs-breakpoints: (
	mobile: 320px,
	tablet: 541px,
	desktop: 769px
) !default;

// Media query helper
@mixin fs-mq($from: null, $to: null, $and: null) {
	$map: $fs-breakpoints;
	$query: "";
	@if map.has-key($map, $from) {
		$query: "#{$query} (min-width: #{map.get($map, $from)})";
	}
	@if $to {
		@if map.has-key($map, $from) and map.has-key($map, $to) {
			$query: "#{$query} and";
		}
		@if map.has-key($map, $to) {
			$query: "#{$query} (max-width: #{map.get($map, $to) - 1px})";
		}
	}
	@if $and {
		$query: "#{$query} #{$and}";
	}
	@media screen and #{$query} {
		@content;
	}
}